import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
// @mui
import {
  Table,
  Stack,
  Paper,
  Button,
  Popover,
  Checkbox,
  TableRow,
  MenuItem,
  TableBody,
  TableCell,
  Typography,
  IconButton,
  TableContainer,
  TablePagination,
  Box,
  LinearProgress,
  Chip,
  Divider,
} from '@mui/material';

//  third-party
import { filter, uniq, includes, size } from 'lodash';

// components
import Iconify from 'components/iconify';
import MainCard from 'components/cards/MainCard'
import { fDate } from 'utils/formatTime'
// sections
import { ProductListHead, ProductListToolbar } from 'components/products';
// mock

import { PRODUCTS, CATEGORY, STATUS } from '__mocks__/products';
import Image from 'mui-image';
import PageContainer from 'layouts/PageContainer';
import PageHeading from 'layouts/PageHeading';
// ----------------------------------------------------------------------

const TABLE_HEAD = [
  { id: 'name', label: 'Name', alignRight: false },
  { id: 'Stock', label: 'Stock', alignRight: false },
  { id: 'price', label: 'Price', alignRight: false },
  { id: 'createdAt', label: 'Created', alignRight: false },
  { id: 'Status', label: 'Status', alignRight: false },
  { id: '' },
];

// ----------------------------------------------------------------------

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query, filterConditions) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query || filterConditions) {
    return filter(array, (_item) => {
      // console.log("++++++++",includes(size(filterConditions.category)===0? CATEGORY : filterConditions.category, _item.category) );

      return (_item.name.toLowerCase().indexOf(query.toLowerCase()) !== -1) &&
        includes(size(filterConditions.category) === 0 ? CATEGORY : filterConditions.category, _item.category) &&
        includes(size(filterConditions.status) === 0 ? STATUS : filterConditions.status, _item.status)
    });
  }
  return stabilizedThis.map((el) => el[0]);
}

export default function UserPage() {
  const navigate = useNavigate();
  const [open, setOpen] = useState(null);

  const [page, setPage] = useState(0);

  const [order, setOrder] = useState('asc');

  const [selected, setSelected] = useState([]);

  const [orderBy, setOrderBy] = useState('name');

  const [filterName, setFilterName] = useState('');
  const [filterConditions, setFilterConditions] = useState({
    category: [],//CATEGORY,
    status: []//STATUS
  });
  const [rowsPerPage, setRowsPerPage] = useState(5);

  const items = PRODUCTS;

  const handleOpenMenu = (event) => {
    setOpen(event.currentTarget);
  };

  const handleCloseMenu = () => {
    setOpen(null);
  };

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = items.map((n) => n.name);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setPage(0);
    setRowsPerPage(parseInt(event.target.value, 10));
  };

  const handleFilterByName = (event) => {
    setPage(0);
    setFilterName(event.target.value);
  };
  const handleFilterCondition = (type, condition) => {
    setPage(0);

    setFilterConditions((preConditions) => {
      let category = preConditions.category;
      let status = preConditions.status;
      if (type.toLowerCase().indexOf('category') !== -1) {
        category = uniq([...preConditions.category, condition])
      }
      if (type.toLowerCase().indexOf('status') !== -1) {
        status = uniq([...preConditions.status, condition])
      }
      return { category, status }
    })
  };
  // console.log("filterConditions>>> ",filterConditions)
  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - items.length) : 0;

  const filteredItems = applySortFilter(items, getComparator(order, orderBy), filterName, filterConditions);

  const isNotFound = !filteredItems.length && !!filterName;

  const handleConditionDelete = (type, condition) => {
    console.info('You clicked the delete icon.', type, condition);

    setPage(0);
    setFilterConditions((preConditions) => {
      let category = preConditions.category;
      let status = preConditions.status;
      if (type?.toLowerCase()?.indexOf('category') !== -1) {
        category = filter(category, (_item) => _item?.indexOf(condition) === -1)
      }
      if (type?.toLowerCase()?.indexOf('status') !== -1) {
        status = filter(status, (_item) => _item?.indexOf(condition) === -1)
      }

      return { category, status }
    })
  };

  return (
    <PageContainer>
      <PageHeading title="Products" action={
        <Button variant="contained" startIcon={<Iconify icon="eva:plus-fill" />}
          onClick={() => navigate('/pages/products/add', { replace: true })}
        >
          New Product
        </Button>
      } />
      <Box sx={{ flexGrow: 1 }}>
        <MainCard sx={{ flexGrow: 1, pt: 0 }}>
          <Stack direction='row' spacing={1} sx={{ ml: 3 }}>
            {filterConditions.category.map((item) => (
              <Chip key={item} label={
                <Stack direction={'row'}>
                  <Typography>Category: </Typography>
                  <Typography color='warning.main'>{item}</Typography>
                </Stack>
              }
                onDelete={() => handleConditionDelete('category', item)}
                sx={{
                  borderRadius: 2
                }}
              />
            ))}

            {filterConditions.status.map((item) => (
              <Chip key={item} label={
                <Stack direction={'row'}>
                  <Typography>Status: </Typography>
                  <Typography color='warning.main'>{item}</Typography>
                </Stack>
              }
                onDelete={() => handleConditionDelete('status', item)}

              />

            ))}
            <Divider />
          </Stack>
          <ProductListToolbar numSelected={selected.length} filterName={filterName} onFilterName={handleFilterByName} onFilterCondition={handleFilterCondition} />

          {/* <Scrollbar> */}
          <TableContainer >
            <Table>
              <ProductListHead
                order={order}
                orderBy={orderBy}
                headLabel={TABLE_HEAD}
                rowCount={items.length}
                numSelected={selected.length}
                onRequestSort={handleRequestSort}
                onSelectAllClick={handleSelectAllClick}
              />
              <TableBody>
                {filteredItems.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                  const { id, name, media, stock, price, status, createdAt } = row;
                  const selectedUser = selected.indexOf(name) !== -1;

                  return (
                    <TableRow hover key={id} tabIndex={-1} role="checkbox" selected={selectedUser}>
                      <TableCell padding="checkbox">
                        <Checkbox checked={selectedUser} onChange={(event) => handleClick(event, name)} />
                      </TableCell>

                      <TableCell component="th" scope="row" padding="none">
                        <Stack direction="row" alignItems="center" spacing={2}>
                          <Image src={media} shift="left" width={48} height={48} sx={{ display: { sm: 'none', lg: 'inline' } }} />
                          <Typography variant="subtitle2" noWrap>
                            {name}
                          </Typography>
                        </Stack>
                      </TableCell>

                      <TableCell align="left">
                        <LinearProgress
                          variant="determinate"
                          color={stock < 10 ? 'error' : 'info'}
                          value={stock} />
                        {stock}
                      </TableCell>

                      <TableCell align="left">${price}</TableCell>

                      <TableCell align="left">{fDate(createdAt)}
                      </TableCell>
                      <TableCell align="left">
                        {/* <Label color={(status === 'draft' && 'error') || 'success'}>{sentenceCase(status)}</Label> */}
                        <Chip label={status} color={status === 'Draft' ? 'warning' : status === 'Archived' ? 'secondary' : 'info'} />
                      </TableCell>

                      <TableCell align="right">
                        <IconButton size="large" color="inherit" onClick={handleOpenMenu}>
                          <Iconify icon={'eva:more-vertical-fill'} />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  );
                })}
                {emptyRows > 0 && (
                  <TableRow style={{ height: 53 * emptyRows }}>
                    <TableCell colSpan={6} />
                  </TableRow>
                )}
              </TableBody>

              {isNotFound && (
                <TableBody>
                  <TableRow>
                    <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                      <Paper
                        sx={{
                          textAlign: 'center',
                        }}
                      >
                        <Typography variant="h6" paragraph>
                          Not found
                        </Typography>

                        <Typography variant="body2">
                          No results found for &nbsp;
                          <strong>&quot;{filterName}&quot;</strong>.
                          <br /> Try checking for typos or using complete words.
                        </Typography>
                      </Paper>
                    </TableCell>
                  </TableRow>
                </TableBody>
              )}
            </Table>
          </TableContainer>
          {/* </Scrollbar> */}

          <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={filteredItems.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </MainCard>
        {/* </Container> */}

        <Popover
          open={Boolean(open)}
          anchorEl={open}
          onClose={handleCloseMenu}
          anchorOrigin={{ vertical: 'top', horizontal: 'left' }}
          transformOrigin={{ vertical: 'top', horizontal: 'right' }}
          PaperProps={{
            sx: {
              p: 1,
              width: 140,
              '& .MuiMenuItem-root': {
                px: 1,
                typography: 'body2',
                borderRadius: 0.75,
              },
            },
          }}
        >
          <MenuItem>
            <Iconify icon={'eva:edit-fill'} sx={{ mr: 2 }} />
            Edit
          </MenuItem>

          <MenuItem sx={{ color: 'error.main' }}>
            <Iconify icon={'eva:trash-2-outline'} sx={{ mr: 2 }} />
            Delete
          </MenuItem>
        </Popover>
      </Box>
    </PageContainer>
  );
}
